export function Trois({info}){
    return <>
        <div className="day">
            
            <table>
                <tr>
                    <th className="th_title" colSpan={2}>Lundi</th>
                </tr>
                <tr>
                    <th>{info.exercices.jambe[0]}</th>
                    <td>4 sries de 6  8 rptitions</td>
                </tr>
                <tr>
                    <th>{info.exercices.dos[0]}</th>
                    <td>4 sries de 6  8 rptitions</td>
                </tr>
                <tr>
                    <th>{info.exercices.pec[0]}</th>
                    <td>4 sries de 6  8 rptitions</td>
                </tr>
                <tr>
                    <th>Elevations latrales</th>
                    <td>3 sries de 10  15 rptitions</td>
                </tr>

            </table>

        </div>
        <div className="day">
            <table>
                <tr>
                    <th className="th_title">Mercredi</th>
                </tr>
                <tr>
                    <th>{info.exercices.jambe[4]}</th>
                    <td>4 sries de 6  8 rptitions</td>
                </tr>
                <tr>
                    <th>{info.exercices.dos[3]}</th>
                    <td>4 sries de 6  8 rptitions</td>
                </tr>
                <tr>
                    <th>{info.exercices.pec[3]}</th>
                    <td>4 sries de 6  8 rptitions</td>
                </tr>
                <tr>
                    <th>Elevations latrales</th>
                    <td>3 sries de 10  15 rptitions</td>
                </tr>

            </table>
        </div>
        <div className="day">
            <table>
                <tr>
                    <th className="th_title">Vendredi</th>
                </tr>
                <tr>
                    <td>Effectuer l'avant dernire sance (car vous effectuer une mme sance une fois sur deux.)</td>
                </tr>
            </table>
        </div>
    </>
}